<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <div slot="left" color="#3051f1">
        <a @click="toBack">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">{{title}}</span>
      </div>
    </mu-appbar>
    <div class="content">
      <div class="moudle">
        <div class="titleContent" :style="'border-left-color:'+$store.state.color">易耗品使用记录</div>
        <div class="contentmoudle" v-for="(item,index) in contentList" :key="index">
          <mu-expansion-panel
            :expand="panel === 'panel1'+index"
            @change="toggle('panel1'+index)"
            class="border"
          >
            <div slot="header">易耗品名称：{{item.consumable}}</div>
            <div class="flexRow">
              <div>
                <span>易耗品名称</span>
              </div>
              <div>
                <span>{{item.consumable}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div>
                <span>时间</span>
              </div>
              <div>
                <span>{{item.gmtCreate}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div>
                <span>数量</span>
              </div>
              <div>
                <span>{{item.number}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div>
                <span>运维号</span>
              </div>
              <div>
                <span>{{item.operateId}}</span>
              </div>
            </div>
          </mu-expansion-panel>
        </div>
      </div>
      <div class="moudle padding-bottom">
        <div class="titleContent" :style="'border-left-color:'+$store.state.color">例行巡检文档</div>
        <div class="contentmoudle" v-for="(item,index) in contentList1" :key="index">
          <mu-expansion-panel
            :expand="panel === 'panel2'+index"
            @change="toggle('panel2'+index)"
            class="border"
          >
            <div slot="header">
              运维：
              <span style="font-size:14px;">{{item.name}}</span>
            </div>
            <div class="flexRow">
              <div>
                <span>文件ID号</span>
              </div>
              <div>
                <span>{{item.fileId}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div>
                <span>运维时间</span>
              </div>
              <div>
                <span>{{item.gmtCreate}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div @click="toWebview(item.path)">
                <span style="color:blue">{{item.name}}</span>
              </div>
              <!-- <div>
                <span>{{item.path}}</span>
              </div>-->
            </div>
          </mu-expansion-panel>
        </div>
      </div>
      <div class="moudle">
        <div class="titleContent" :style="'border-left-color:'+$store.state.color">仪器档案</div>
        <div class="contentmoudle" v-for="(item,index) in contentListinstrument" :key="index">
          <mu-expansion-panel
            :expand="panel === 'panel3'+index"
            @change="toggle('panel3'+index)"
            class="border"
          >
            <div slot="header">系统名称：{{item.instrumentName}}</div>
            <div class="flexRow">
              <div>
                <span>测试文档</span>
              </div>
              <div @click="toWebview(item.debugDoc[1])">
                <span style="color:blue">{{item.debugDoc[0]}}</span>
              </div>
            </div>
            <div class="flexRow">
              <div>
                <span>安装</span>
              </div>
              <div>
                <span>{{item.installer}}</span>
              </div>
            </div>
            <div class="flexRow" v-for="(item1,index1) in item.maintenanceFile" :key="index1">
              <mu-expansion-panel
                :expand="panel1 === 'panel8'+index1"
                @change="toggle1('panel8'+index1)"
                class="widthContent"
              >
                <div slot="header">维护保修文档</div>
                <div class="flexRow">
                  <div>
                    <span>文件ID号</span>
                  </div>
                  <div>
                    <span>{{item1.fileId}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>时间</span>
                  </div>
                  <div>
                    <span>{{item1.gmtCreate}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>名称</span>
                  </div>
                  <div @click="toWebview1(item1.path)">
                    <span style="color:blue">{{item1.name}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>站点</span>
                  </div>
                  <div>
                    <span>{{item1.station}}</span>
                  </div>
                </div>
              </mu-expansion-panel>
            </div>
            <div class="flexRow" v-for="(item1,index1) in item.checkoutDoc" :key="index1">
              <mu-expansion-panel
                :expand="panel1 === 'panel9'+index1"
                @change="toggle1('panel9'+index1)"
                class="widthContent"
              >
                <div slot="header">签出文档{{index1+1}}</div>
                <div class="flexRow">
                  <div>
                    <span>文件ID号</span>
                  </div>
                  <div>
                    <span>{{item1.fileId}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>时间</span>
                  </div>
                  <div>
                    <span>{{item1.gmtCreate}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>名称</span>
                  </div>
                  <div @click="toWebview1(item1.path)">
                    <span style="color:blue">{{item1.name}}</span>
                  </div>
                </div>
                <div class="flexRow">
                  <div>
                    <span>站点</span>
                  </div>
                  <div>
                    <span>{{item1.station}}</span>
                  </div>
                 
                </div>
              </mu-expansion-panel>
            </div>
          </mu-expansion-panel>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
var url = "http://47.112.16.168:";
export default {
  name: "detailsNews",
  data() {
    return {
      title: this.$route.query.name,
      token: "",
      contentList: [],
      panel: "",
      panel1: "",
      contentList1: [],
      contentListinstrument: [],
      maintenanceFile: []
    };
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    var name = this.$route.query.name;
    this.initData(name);
  },
  methods: {
    toDebugDocWeb(e) {
      window.open(url + e);
    },
    toWebview1(e) {
      window.open(url + e);
    },
    toWebview(e) {
      // alert(e);
      window.open(url + e);
    },
    toBack() {
      var name = this.$route.query.id;
      if (name === "Event") {
        this.$router.replace("/Event");
      } else {
        this.$router.push({
          path: "/Navigation",
          query: {
            id: 2
          }
        });
      }
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    toggle1(panel) {
      this.panel1 = panel === this.panel1 ? "" : panel;
    },

    async initData(e) {
      var url = "/system/file/technical";
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res);
      var array = [];
      array = res.data.data;
      for (let index = 0; index < array.length; index++) {
        var station = array[index].station;
        if (e === station) {
          console.log(station);
          console.log(array[index]);
          this.contentList = array[index].consumableRecords;
          this.contentList1 = array[index].inspectFile;
          this.contentListinstrument = array[index].instrumentFiles;
          // this.maintenanceFile = array[index].instrumentFiles;
          for (let i = 0; i < this.contentListinstrument.length; i++) {
            var files = this.contentListinstrument[i].maintenanceFile;
            this.contentListinstrument[i].debugDoc = this.contentListinstrument[
              i
            ].debugDoc.split(",");
            // this.maintenanceFile = files;
          }
          console.log(this.contentList);
        }
      }
    }
  }
};
</script>
<style scoped>
.content {
  padding: 10px;
  margin-top: 50px;
}
.moudle {
  width: 100%;
  background: white;
  margin: 10px 0;
  border-radius: 5px;
  /* height: 200px; */
  height: 100%;
  padding: 10px;
  /* padding-bottom:0;  */
}
.titleContent {
  border-left: #4ed995 3px solid;
  padding-left: 10px; 
}
.flexRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px 0;
  color: rgb(100, 100, 100);
}
.border {
  border: none;
  padding: 0 10px;
  box-shadow: none;
  border-bottom: 1px solid rgb(245, 245, 245);
}
.widthContent {
  width: 100%;
  box-shadow: none;
}
.header {
  position: fixed;
  top: 0;
}
.mu-expansion-panel__expand .mu-expansion-panel-header{
  min-height: 50px;
}
</style>



